@import url(https://fonts.googleapis.com/css2?family=Comic+Neue&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: 0;
  font-family: 'Comic Neue', cursive;
}
.region-page {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.region-page nav {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.region-page nav button {
  background-color: white;
  border-radius: 15px;
  height: 30px;
  width: 100px;
  margin-bottom: 40px;
}

.region-page input {
  width: 60%;
  height: 40px;
  border-radius: 15px;
  display: flex;
  margin-bottom: 40px;
}
.Card img {
  width: 200px;
  height: 200px;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.Card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 300px;
  background-color: #f1f1f1;
  flex-basis: 19%;
  margin-bottom: 15px;
  text-decoration: none;
  border-radius: 15px;
  margin-left: 20px;
  border: none;
  cursor: pointer;
  padding-top: 10px;
  font-size: 20px;
}

.teste {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 15px;
  margin-left: 20px;
  cursor: pointer;
}

.Card:hover {
  box-shadow: 0 0 1em red;
}

.Card h3 {
  color: black;
}

.Card article {
  display: flex;
  margin-top: 10px;
}

.Card article p {
  border-radius: 15px;
  padding: 6px;
  margin-left: 6px;
}

