  background-color: rgb(85, 0, 0);
  color: white;
  font-weight: bold;
}

.fighting {
  background-color: rgb(161, 1, 1);
  color: white;
  font-weight: bold;
}

.psychic {
  background-color: rgb(250, 125, 146);
  color: white;
