}

.MainPage {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.title-image {
  align-items: center;
  margin-bottom: 5%;
  width: 50%;
}

.ash {
  width: 15%;
}

.girl {
  width: 20%;
}

.main-image-container {
  display: flex;
  justify-content: space-between;
}

.main-button-container {
  display: flex;
  justify-content: space-between;
  width: 50%;
}

.main-button-container button {
  border: none;
  border-radius: 30em;
  box-shadow: 6px 6px 12px #c5c5c5 , -6px -6px 12px white;
  font-family: inherit;
  font-size: 15px;
  height: 3em;
  overflow: hidden;
  position: relative;
  width: 9em;
  z-index: 1;
}

.main-button-container button:before {
  background-image: linear-gradient(to right, #0fd850 0%, #f9f047 100%);
  border-radius: 30em;
  content: '';
  display: block;
  height: 3em;
  left: 0;
  position: absolute;
  top: 0;
  transition: 0.5s ease;
  width: 0;
  z-index: -1;
}

.main-button-container button:hover:before {
  width: 9em;
}

.Details {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.Details h1 {
  text-align: center;
}

.main-content {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 100px;
}

.main-content-image {
  width: 25%;
}

.image-container {
  display: flex;
  flex-direction: column;
}

.shiny {
  width: 100%;
  border: 1px solid black;
  border-radius: 15px;
  background-color: #f1f1f1;
}

.type {
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
}

.type h3 {
  border: 2px solid black;
  border-radius: 15px;
  padding: 20px;
}

section {
  border: 1px solid black;
  width: 15%;
  padding: 20px;
  background-color: #f1f1f1;
  border-radius: 15px;
} 

.shiny-card {
  width: 20%;
}

.shiny-card h3 {
  text-align: center;
  margin-bottom: 10px;
}

footer {
  bottom: 0;
  position: fixed;
}

.back {
  background-color: red;
  font-weight: bold;
  color: white;
  width: 20%;
  height: 50px;
  border-radius: 15px;
  border: 1px solid white;
  align-self: center;
  cursor: pointer;
}

.favorite {
  background-color: green;
  font-weight: bold;
  color: white;
  width: 20%;
  height: 50px;
  border-radius: 15px;
  border: 1px solid white;
  align-self: center;
  margin-left: 15px;
  cursor: pointer;
}

.favorite:disabled {
  background-color: green;
  opacity: 0.4;
  font-weight: bold;
  color: white;
  width: 20%;
  height: 50px;
  border-radius: 15px;
  border: 1px solid white;
  align-self: center;
  margin-top: 10%;
}

.button-container {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
  margin-top: 5%;
}

.error-message {
  display: flex;
  flex-direction: column;
}

.error-message button {
  margin-top: 30px;
}

.error-message p, h3 {
  text-align: center;
}

.error-message h3 {
  margin-bottom: 20px;
}

.Favorite {
  display: flex;
  flex-direction: column;
}

.Favorite h1 {
  text-align: center;
}

.favorite-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 30px;
}

.favorite-card {
  display: flex;
  flex-direction: column;
  width: 33%;
  margin-bottom: 20px;
  align-items: center;
}

.favorite-card img {
  width: 200px;
}

.remove-button {
  font-weight: bold;
  margin-top: 10px;
  border: none;
  background-color: white;
  cursor: pointer;
}

.remove-button img {
  width: 30px;
}

.favorite-image-container {
  display: flex;
  bottom: 0;
  position: fixed;
  justify-content: space-between;
}

.pikachu {
  width: 10%;
  height: 15%;
  align-self: flex-end;
}

.rocket {
  width: 15%;
  bottom: 0;
}

.trio {
  width: 20%;
}
